import React, { useEffect, useState } from "react";
import { Collection,getDoc } from "firebase/firestore";
import { db } from "@/firebase/firebase";

export default function TrangChu() {
  cosnt[products,setProducts] = useState([]);
  const fetchProducts = async () => {
    try {
      const snapshot = await getDocs(collection(db, "products"));
      const data = snapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data()
      }));
      setProducts(data);
    } catch (error) {
      console.error("Lỗi khi lấy sản phẩm:", error);
    }
  };

  useEffect(() => {
    fetchProducts();
  }, []);
  return (
    <div>
      <section className="banner">[Slider ảnh]</section>

      <section className="danh-muc">
        <h2>Danh mục sản phẩm</h2>
        <div>[Các danh mục]</div>
      </section>

      <section className="san-pham-noi-bat">
        <h2>Sản phẩm nổi bật</h2>
        <div>[Danh sách sản phẩm]</div>
      </section>
      <section className="product" >
        <h2>Danh Sách Sản Phẩm</h2>
        <div style={{ display: "flex", flexWrap: "wrap", gap: "20px" }}>
        {products.map(product => (
          <div key={product.id} style={{ border: "1px solid #ccc", padding: 10, width: 200 }}>
            <img src={product.imageUrl} alt={product.name} width="100%" />
            <h4>{product.name}</h4>
            <p>{product.description}</p>
            <strong>{product.price.toLocaleString()} đ</strong>
          </div>
        ))}
        </div>
      </section>

    
    </div>
  );
}
